@import url(https://fonts.googleapis.com/css2?family=Piazzolla:wght@600&display=swap);
body {
  margin: 0;
  font-family: 'Piazzolla', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#main-container {
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-direction: column;
       -moz-box-orient: vertical;
       -moz-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
       -moz-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-align-items: center;
       -moz-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    background-color: #FFF6F0;
    color: #444;
    padding: 1.5em;
}

h1, h2, h3, h4, h5, h6, p {
    margin: 0;
    text-align: center;
}

h2 {
    border: 3px solid #888;
    -webkit-border-radius: 8px;
       -moz-border-radius: 8px;
            border-radius: 8px;
    padding: 0.15em;
    width: 4.5em;
    font-size: 1.7em;
    margin: 0.5em 0 0 0;
    background-color: #FFFBF8;
    -webkit-box-shadow: 0px 2px 4px #444;
       -moz-box-shadow: 0px 2px 4px #444;
            box-shadow: 0px 2px 4px #444;
}

h3 {
    font-size: 1.5em;
}

#main-container > div {
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-direction: row;
       -moz-box-orient: horizontal;
       -moz-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
       -moz-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-align-items: flex-start;
       -moz-box-align: start;
        -ms-flex-align: start;
            align-items: flex-start;
}

.league-column {
    margin: 0.5em 1.5em;
}

input[type="button"] {
    font-family: inherit;
    padding: 0.2em 1em;
    font-size: 1em;
    margin: 1.5em 0.5em;
}

.seed {
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-direction: row;
       -moz-box-orient: horizontal;
       -moz-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
       -moz-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-align-items: center;
       -moz-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    margin: 0.5em;
}

.seed:nth-child(n+9) {
    position: absolute;
}

.seed:nth-child(n+9) img, .seed:nth-child(n+9) p {
    opacity: 0;
}

.seed p {
    margin-right: 1em;
    font-size: 1.4em;
    width: 1.5em;
}

.seed img {
    width: 4em;
    -webkit-border-radius: 6px;
       -moz-border-radius: 6px;
            border-radius: 6px;
    -webkit-box-shadow: 0px 0px 4px #444;
       -moz-box-shadow: 0px 0px 4px #444;
            box-shadow: 0px 0px 4px #444;
}

#source {
    margin-top: 2.5em;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    font-weight: 600;
    font-size: 1.1em;
    color: #756B60;
}

#references {
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-direction: column !important;
       -moz-box-orient: vertical !important;
       -moz-box-direction: normal !important;
        -ms-flex-direction: column !important;
            flex-direction: column !important;
    -webkit-justify-content: center !important;
       -moz-box-pack: center !important;
        -ms-flex-pack: center !important;
            justify-content: center !important;
    -webkit-align-items: center !important;
       -moz-box-align: center !important;
        -ms-flex-align: center !important;
            align-items: center !important;
}

#references span {
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-direction: row;
       -moz-box-orient: horizontal;
       -moz-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
       -moz-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-align-items: center;
       -moz-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    font-weight: 600;
    font-size: 1em;
    color: #756B60;
}

#references p {
    margin-right: 0.33em;
}

a {
    color: inherit;
    text-decoration: none;
}

